.App {
  padding-top: 50px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  width: 100%;
}
.App .col {
  width: 50%;
  padding: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.input-container {
  text-align: left;
}

.console {
  margin-top: 40px;
  padding: 20px;
  height: 250px;
  color: rgb(0, 255, 0);
  width: 70% !important;
  overflow: scroll;
  background-color: rgb(17, 17, 17);
}

textarea {
  width: 100%;
  height: 400px;
}
